import React, {useContext} from 'react'
import {Context} from './Context'
import image from './trash-bin.png'

function Completed(){
    const {completed, handleCompleted} = useContext(Context)

    const displayItems = completed.map((item, index)=>(
        <div key={index} className='todoItem'>
           <p>{item}</p>
           <img onClick={()=>handleCompleted(index)} className="todoDone" src={image}/>

        </div>
    ))

    return (
        <div className='todoContainer'>
        <h3 style={{color:completed.length>0? 'green': '#E8070B'}}>Tasks completed: {completed.length}</h3>
        {displayItems}
        </div>
    )
}

export default Completed