import React, {useContext} from 'react'
import {Context} from './Context'
import Storager from './Storager'
 
function Todo(props){
    
    const {handleClick, storage, todo, handleChange, focusInput} = useContext(Context)

    const arrayDisplay = storage.map((item, index) => (<Storager key={index} item={item} index={index} />))

    return (<div className='todoContainer'>
      <form>
      <input type='text' ref={focusInput} value={todo} onChange={handleChange}/>
      <button onClick={handleClick} className='todoAdd'>Add</button>
      </form>
      {arrayDisplay}
  
    </div>)
}

export default Todo

