import React, {useContext,useState} from 'react'
import {Context} from './Context'

function Storager(props){
    const {handleDone}= useContext(Context)
    const [hovered, setHovered] = useState(false)

    function toggleHovered(){
        if(hovered){
            return '✔️'
        }
        else {
            return '❌'
        }
    }
    return(
        <div key={props.key} className="todoItem" >
            <p className={hovered ? 'hoveredTodo' : 'notHoveredTodo'}>{props.item}</p>
            <button 
            className="todoDone"
            onClick={()=>handleDone(props.index)}
            onMouseEnter={()=>setHovered(true)}
            onMouseLeave={()=>setHovered(false)}
            >{toggleHovered()}</button>
        </div>
    )
}

export default Storager

